.size {
    display: inline-block;
    vertical-align: top;
    height: rem(30);
    border: $border_width_2x solid $black;
    padding: rem(1) rem(8);
    font-size: rem(16);
    text-transform: uppercase;
    position: relative;

    &:before,
    &:after {
        content: ' ';
        display: block;
        position: absolute;
        transition: background-color $trans_sm;
    }

    &:before {
        top: 50%;
        left: 0;
        right: 0;
        margin-top: -1px;
        height: $border_width_2x;
    }

    &:after {
        top: 0;
        left: 50%;
        bottom: 0;
        margin-left: -1px;
        width: $border_width_2x;
    }

    &:hover {
        @include screen_1200_min_width {
            &:before,
            &:after {
                background-color: $black;
            }
        }
    }

    &_active {
        cursor: default;
        &:before,
        &:after {
            background-color: $black;
        }
    }
}
